.projects {
  padding: 5rem 2rem;
  position: relative;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
}

.grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
  gap: 2rem;
  margin-top: 3rem;
}

.projectCard {
  background: var(--glass);
  backdrop-filter: var(--backdrop-blur);
  border: 1px solid var(--glass-border);
  border-radius: var(--border-radius);
  overflow: hidden;
  transition: var(--transition);
  position: relative;
  cursor: pointer;
  text-decoration: none;
  color: inherit;
  height: 400px;
  display: flex;
  flex-direction: column;
}

.projectCard:hover {
  transform: translateY(-10px);
  box-shadow: var(--shadow-lg), var(--shadow-glow);
}

.imageWrapper {
  position: relative;
  height: 200px;
  overflow: hidden;
  background: var(--surface);
}

.projectImage {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: var(--transition-slow);
}

.projectCard:hover .projectImage {
  transform: scale(1.1);
}

.overlay {
  position: absolute;
  inset: 0;
  background: linear-gradient(
    135deg,
    rgba(99, 102, 241, 0.1) 0%,
    rgba(139, 92, 246, 0.1) 100%
  );
  opacity: 0;
  transition: var(--transition);
  display: flex;
  align-items: center;
  justify-content: center;
}

.projectCard:hover .overlay {
  opacity: 1;
}

.overlayContent {
  display: flex;
  gap: 1rem;
  align-items: center;
}

.overlayBtn {
  width: 3rem;
  height: 3rem;
  border-radius: 50%;
  background: rgba(255, 255, 255, 0.9);
  border: none;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: var(--transition);
  backdrop-filter: var(--blur);
}

.overlayBtn:hover {
  transform: scale(1.1);
  background: white;
}

.overlayBtn svg {
  width: 1.25rem;
  height: 1.25rem;
  color: var(--primary);
}

.content {
  padding: 1.5rem;
  flex-grow: 1;
  display: flex;
  flex-direction: column;
}

.title {
  font-size: 1.5rem;
  font-weight: 700;
  margin-bottom: 0.5rem;
  color: var(--text-primary);
  background: var(--gradient-primary);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.description {
  color: var(--text-secondary);
  line-height: 1.6;
  margin-bottom: 1rem;
  flex-grow: 1;
}

.tech {
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
  margin-bottom: 1rem;
}

.techTag {
  padding: 0.25rem 0.75rem;
  background: var(--surface);
  border: 1px solid var(--glass-border);
  border-radius: var(--border-radius-sm);
  font-size: 0.75rem;
  color: var(--text-secondary);
  font-weight: 500;
}

.actions {
  display: flex;
  gap: 1rem;
  align-items: center;
}

.actionBtn {
  padding: 0.5rem 1rem;
  border: 1px solid var(--glass-border);
  border-radius: var(--border-radius-sm);
  background: var(--surface);
  color: var(--text-primary);
  text-decoration: none;
  font-size: 0.875rem;
  font-weight: 600;
  transition: var(--transition);
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.actionBtn:hover {
  background: var(--primary);
  color: white;
  border-color: var(--primary);
  transform: translateY(-2px);
}

.actionBtn svg {
  width: 1rem;
  height: 1rem;
}

.featured {
  grid-column: span 2;
  height: 500px;
}

.featured .imageWrapper {
  height: 250px;
}

.featuredBadge {
  position: absolute;
  top: 1rem;
  right: 1rem;
  padding: 0.5rem 1rem;
  background: var(--gradient-primary);
  color: white;
  border-radius: var(--border-radius-sm);
  font-size: 0.75rem;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 0.5px;
  z-index: 2;
}

/* Loading skeleton */
.skeleton {
  background: linear-gradient(90deg, var(--surface) 25%, var(--surface-hover) 50%, var(--surface) 75%);
  background-size: 200% 100%;
  animation: loading 1.5s infinite;
}

@keyframes loading {
  0% { background-position: 200% 0; }
  100% { background-position: -200% 0; }
}

/* Filter buttons */
.filters {
  display: flex;
  gap: 1rem;
  justify-content: center;
  margin-bottom: 3rem;
  flex-wrap: wrap;
}

.filterBtn {
  padding: 0.75rem 1.5rem;
  border: 1px solid var(--glass-border);
  border-radius: var(--border-radius-lg);
  background: var(--glass);
  color: var(--text-secondary);
  cursor: pointer;
  transition: var(--transition);
  font-weight: 600;
  backdrop-filter: var(--blur);
}

.filterBtn:hover,
.filterBtn.active {
  background: var(--primary);
  color: white;
  border-color: var(--primary);
  transform: translateY(-2px);
}

/* Responsive */
@media (max-width: 768px) {
  .grid {
    grid-template-columns: 1fr;
    gap: 1.5rem;
  }
  
  .featured {
    grid-column: span 1;
    height: 400px;
  }
  
  .featured .imageWrapper {
    height: 200px;
  }
  
  .projects {
    padding: 3rem 1rem;
  }
}