.hero {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  overflow: hidden;
  padding: 2rem;
}

/* Animated background particles */
.particles {
  position: absolute;
  inset: 0;
  overflow: hidden;
  z-index: -1;
}

.particle {
  position: absolute;
  width: 4px;
  height: 4px;
  background: var(--primary);
  border-radius: 50%;
  opacity: 0.5;
  animation: float-particle 20s infinite linear;
}

@keyframes float-particle {
  from {
    transform: translateY(100vh) translateX(0);
  }
  to {
    transform: translateY(-100vh) translateX(100px);
  }
}

.content {
  max-width: 1200px;
  width: 100%;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 4rem;
  align-items: center;
  z-index: 1;
}

.textContent {
  animation: fadeInLeft 1s ease-out;
}

.greeting {
  display: inline-block;
  padding: 0.5rem 1.5rem;
  background: var(--glass);
  border: 1px solid var(--glass-border);
  border-radius: var(--border-radius-lg);
  font-size: 0.875rem;
  color: var(--primary);
  font-weight: 600;
  margin-bottom: 1rem;
  backdrop-filter: var(--blur);
  animation: fadeInUp 0.8s ease-out;
}

.title {
  font-size: clamp(3rem, 6vw, 5rem);
  font-weight: 900;
  line-height: 1;
  margin-bottom: 1rem;
  letter-spacing: -0.04em;
}

.titleGradient {
  background: var(--gradient-primary);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  display: inline-block;
  animation: gradient-shift 3s ease infinite;
}

@keyframes gradient-shift {
  0%, 100% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
}

.subtitle {
  font-size: clamp(1.25rem, 2vw, 1.75rem);
  color: var(--text-secondary);
  margin-bottom: 1.5rem;
  font-weight: 400;
}

.description {
  font-size: 1.125rem;
  line-height: 1.8;
  color: var(--text-secondary);
  margin-bottom: 2rem;
  max-width: 500px;
}

.actions {
  display: flex;
  gap: 1rem;
  flex-wrap: wrap;
  margin-bottom: 3rem;
  animation: fadeInUp 1s ease-out 0.2s both;
}

.primaryBtn {
  padding: 1rem 2rem;
  font-size: 1.125rem;
}

.socialLinks {
  display: flex;
  gap: 1rem;
  animation: fadeInUp 1s ease-out 0.4s both;
}

.socialLink {
  width: 3rem;
  height: 3rem;
  display: flex;
  align-items: center;
  justify-content: center;
  background: var(--glass);
  border: 1px solid var(--glass-border);
  border-radius: var(--border-radius-sm);
  color: var(--text-primary);
  text-decoration: none;
  transition: var(--transition);
  backdrop-filter: var(--blur);
  position: relative;
  overflow: hidden;
}

.socialLink::before {
  content: '';
  position: absolute;
  inset: 0;
  background: var(--gradient-primary);
  opacity: 0;
  transition: opacity 0.3s ease;
  z-index: -1;
}

.socialLink:hover {
  transform: translateY(-5px) scale(1.1);
  border-color: var(--primary);
  color: white;
}

.socialLink:hover::before {
  opacity: 1;
}

.socialLink svg {
  width: 1.25rem;
  height: 1.25rem;
}

/* Profile Image Section */
.imageContent {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  animation: fadeInRight 1s ease-out;
}

.profileWrapper {
  position: relative;
  transform-style: preserve-3d;
  animation: float 6s ease-in-out infinite;
}

@keyframes float {
  0%, 100% {
    transform: translateY(0) rotateY(0deg);
  }
  50% {
    transform: translateY(-20px) rotateY(10deg);
  }
}

.profileImage {
  width: 400px;
  height: 400px;
  border-radius: var(--border-radius-lg);
  object-fit: cover;
  position: relative;
  z-index: 2;
  box-shadow: var(--shadow-lg);
}

/* Glass overlay effect */
.profileWrapper::before {
  content: '';
  position: absolute;
  inset: -20px;
  background: var(--gradient-primary);
  opacity: 0.3;
  filter: blur(40px);
  border-radius: var(--border-radius-lg);
  z-index: 1;
  animation: pulse 4s ease-in-out infinite;
}

/* Decorative elements */
.floatingElements {
  position: absolute;
  inset: -50px;
  pointer-events: none;
}

.floatingElement {
  position: absolute;
  background: var(--glass);
  border: 1px solid var(--glass-border);
  backdrop-filter: var(--blur);
  border-radius: var(--border-radius);
}

.floatingElement:nth-child(1) {
  width: 80px;
  height: 80px;
  top: -20px;
  right: -20px;
  animation: float-reverse 8s ease-in-out infinite;
}

.floatingElement:nth-child(2) {
  width: 60px;
  height: 60px;
  bottom: -10px;
  left: -10px;
  animation: float 6s ease-in-out infinite;
}

.floatingElement:nth-child(3) {
  width: 40px;
  height: 40px;
  top: 50%;
  left: -30px;
  animation: float-reverse 7s ease-in-out infinite;
}

@keyframes float-reverse {
  0%, 100% {
    transform: translateY(0) rotate(0deg);
  }
  50% {
    transform: translateY(20px) rotate(180deg);
  }
}

/* Status indicator */
.status {
  position: absolute;
  bottom: 2rem;
  right: 2rem;
  display: flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.75rem 1.5rem;
  background: var(--glass);
  backdrop-filter: var(--blur);
  border: 1px solid var(--glass-border);
  border-radius: var(--border-radius-lg);
  font-size: 0.875rem;
  color: var(--text-secondary);
  z-index: 3;
}

.statusDot {
  width: 8px;
  height: 8px;
  background: #10b981;
  border-radius: 50%;
  animation: pulse 2s ease-in-out infinite;
}

/* Responsive */
@media (max-width: 968px) {
  .content {
    grid-template-columns: 1fr;
    text-align: center;
    gap: 3rem;
  }
  
  .textContent {
    order: 2;
  }
  
  .imageContent {
    order: 1;
  }
  
  .profileImage {
    width: 300px;
    height: 300px;
  }
  
  .description {
    margin: 0 auto 2rem;
  }
  
  .actions,
  .socialLinks {
    justify-content: center;
  }
}

@media (max-width: 640px) {
  .profileImage {
    width: 250px;
    height: 250px;
  }
  
  .title {
    font-size: clamp(2.5rem, 8vw, 3.5rem);
  }
  
  .floatingElements {
    display: none;
  }
}